<template>
  <div class="operational-chart-daily-chart"></div>
</template>

<script>
import _ from 'lodash'
import { mapGetters } from 'vuex'

export default {
  name: 'OperationChartDailyChart',
  data() {
    return {
      chart: null,
      option: {},
      rightYInterval: 40,
      yAxisIndexColor: ['auto', '#6ebac9', '#aad598', '#8c77cd', '#9695e9', '#eb7b80', '#ee8d42']
    }
  },
  // 曲线数据从父组件中获取
  props: {
    seriesData: [],
    xAxisData: [],
    type: '',
    showFlag: true
  },
  watch: {
    seriesData(nv, ov) {
      this.renderChart()
    },
    theme(nv) {
      this.renderChart()
    },
    showFlag() {
      this._resize()
    }
  },
  computed: {
    ...mapGetters(['theme'])
  },
  methods: {
    renderChart() {
      // 此处不清空会导致图表多个系列混淆
      !this.chart || this.chart.clear()
      !!this.chart ||
        (this.chart = this.$echarts.init(document.querySelector('.operational-chart-daily-chart')))
      this.option = this.getOption()
      this.chart.showLoading()
      this.chart.setOption(this.option)
      this.chart.hideLoading()
      // if (!!this.chart) {
      //   this.option = this.getOption()
      //   this.chart.showLoading()
      //   this.chart.setOption(this.option)
      //   this.chart.hideLoading()
      // } else {
      //   this.chart = this.$echarts.init(
      //     document.querySelector('.operational-chart-daily-chart'),
      //     null,
      //     {
      //       renderer: 'svg'
      //     }
      //   )
      //   this.option = this.getOption()
      //   this.chart.showLoading()
      //   this.chart.setOption(this.option)
      //   this.chart.hideLoading()
      // }
    },
    getOption() {
      const series = []
      let dataZoomOpt = {}
      this.seriesData.forEach((e) => {
        const obj = {
          name: e.name,
          type: 'line',
          smooth: true,
          yAxisIndex: e.yAxisIndex,
          // symbol: 'none',
          symbol: 'circle',
          showSymbol: false,
          symbolSize: 8,
          data: e.data,
          itemStyle: {
            color:
              e.name === '发动机转速' ? this.yAxisIndexColor[6] : this.yAxisIndexColor[e.yAxisIndex]
          },
          tooltip: {
            formatter: function (params) {
              return (
                params.name +
                '<br/>' +
                params.seriesName +
                '：' +
                params.value +
                ' ' +
                items[0].Unit
              )
            }
          }
        }
        series.push(obj)
      })
      console.log(series)
      const option = {
        grid: {
          left: '4%',
          right: this.rightYInterval * 5
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            lineStyle: {
              width: 2,
              type: 'line'
            }
          },
          position: function (pt) {
            return [pt[0], '5%']
          }
        },
        title: {
          left: 'center',
          text: '日运维图',
          show: false
        },
        toolbox: {
          show: false,
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: this.type,
          boundaryGap: false,
          data: this.xAxisData,
          axisLine: {
            lineStyle: {
              color: this.theme === 'dark' ? 'white' : 'gray'
            }
          }
        },
        yAxis: [
          {
            type: 'value',
            // boundaryGap: ['20%', '100%'],
            axisLine: {
              show: true,
              lineStyle: {
                // color: this.theme === 'dark' ? 'white' : 'gray'
                color: this.yAxisIndexColor[6]
              }
            },
            splitLine: {
              lineStyle: {
                color: '#ced6e088'
              }
            },
            axisTick: {
              show: true
            },
            // interval: 250,
            // min: 0,
            // max: 2000,
            name: '',
            nameLocation: 'end'
          }
          // {
          //   type: 'value',
          //   position: 'right',
          //   // boundaryGap: ['20%', '100%'],
          //   axisLine: {
          //     show: true,
          //     lineStyle: {
          //       // color: this.theme === 'dark' ? 'white' : 'gray'
          //       // color: 'red'
          //       color: this.yAxisIndexColor[1]
          //     }
          //   },
          //   splitLine: {
          //     show: false
          //   },
          //   min: 0,
          //   max: 1,
          //   name: '功率因素',
          //   nameLocation: 'end',
          //   axisTick: {
          //     show: true
          //   }
          // },
          // {
          //   type: 'value',
          //   position: 'right',
          //   // boundaryGap: ['20%', '100%'],
          //   axisLine: {
          //     show: true,
          //     lineStyle: {
          //       // color: this.theme === 'dark' ? 'white' : 'gray'
          //       color: this.yAxisIndexColor[2]
          //     }
          //   },
          //   splitLine: {
          //     show: false
          //   },
          //   axisTick: {
          //     show: true
          //   },
          //   min: 0,
          //   max: 30,
          //   name: '电池电压',
          //   nameLocation: 'start',
          //   offset: this.rightYInterval
          // },
          // {
          //   type: 'value',
          //   position: 'right',
          //   // boundaryGap: ['20%', '100%'],
          //   axisLine: {
          //     show: true,
          //     lineStyle: {
          //       // color: this.theme === 'dark' ? 'white' : 'gray'
          //       color: this.yAxisIndexColor[3]
          //     }
          //   },
          //   splitLine: {
          //     show: false
          //   },
          //   min: 0,
          //   max: 100,
          //   axisTick: {
          //     show: true
          //   },
          //   name: '水温、液位',
          //   nameLocation: 'end',
          //   offset: this.rightYInterval * 2
          // },
          // {
          //   type: 'value',
          //   position: 'right',
          //   // boundaryGap: ['20%', '100%'],
          //   axisLine: {
          //     show: true,
          //     lineStyle: {
          //       // color: this.theme === 'dark' ? 'white' : 'gray'
          //       color: this.yAxisIndexColor[4]
          //     }
          //   },
          //   splitLine: {
          //     show: false
          //   },
          //   min: 0,
          //   max: 400,
          //   name: '三相电流',
          //   nameLocation: 'start',
          //   axisTick: {
          //     show: true
          //   },
          //   offset: this.rightYInterval * 3
          // },
          // {
          //   type: 'value',
          //   position: 'right',
          //   // boundaryGap: ['20%', '100%'],
          //   axisLine: {
          //     show: true,
          //     lineStyle: {
          //       // color: this.theme === 'dark' ? 'white' : 'gray'
          //       color: this.yAxisIndexColor[5]
          //     }
          //   },
          //   splitLine: {
          //     show: false
          //   },
          //   min: 0,
          //   max: 600,
          //   name: '三相电压',
          //   nameLocation: 'end',
          //   axisTick: {
          //     show: true
          //   },
          //   offset: this.rightYInterval * 4
          // }
        ],
        dataZoom: [
          {
            type: 'inside',
            ...dataZoomOpt
            // minValueSpan: 1000 * 60 * 30
            // minSpan: 5
            // maxSpan: 10
          },
          {
            start: 0,
            end: 100
            // minValueSpan: 1000 * 60 * 30
            // minSpan: 5
            // maxSpan: 10
          }
        ],
        series: series
      }
      return option
    },
    // 重置图表大小
    _resize: _.debounce(function () {
      this.chart.resize()
    }, 300)
  },
  mounted() {
    this.renderChart()
    window.addEventListener('resize', this._resize)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this._resize)
  }
}
</script>

<style lang="less" scoped>
.operational-chart-daily-chart {
  width: 100%;
  height: 100%;
}
</style>


