<script>
/**
 * @desc operation chart(weekly)
 * @auth Demon3443002624@qq.com
 * @date 2022-4-6 11:03:05
 */

import Vue from 'vue'
import dayjs from 'dayjs'
import { mapActions, mapGetters } from 'vuex'

import { formatTime, formatTimeDayjs } from '@/utils/common'
import MyChart from './chart.vue'
import { baseInfoDefault } from './data'
import { isExternal } from '@/utils/validate'

Vue.component('MyCell', {
  props: ['value', 'title', 'isHead', 'col'],
  data() {
    return {
      borderColor: '#7fb1df'
    }
  },
  template: `<div 
  :style="{
      height:'35px',
      flex:col||1,
      }">
        <div v-if="!isHead"
        :style="{
            display:'flex',
            justifyContent:'left',
            alignItems:'center',
            height:'100%',
            borderBottom:'1px solid '+borderColor,
            borderRight:'1px solid '+borderColor,padding:'0 0 0 5px'
        }"
        >{{title}}{{value}}</div>
        <div v-if="!!isHead" 
        :style="{
            display:'flex',
            justifyContent:'center',
            alignItems:'center',
            height:'100%',
            fontSize:'1.2rem',
            fontWeight:'bold',
            borderBottom:'1px solid '+borderColor,
            borderRight:'1px solid '+borderColor,padding:'0 0 0 5px'
        }"
        >{{title}}</div>
      </div>`
})
export default {
  name: 'OperationChartWeekly',
  components: {
    MyChart
  },
  data() {
    return {
      // 机组项目基础信息
      baseInfo: baseInfoDefault,
      // 项目类型
      projectTypeMapping: ['', '运维', '代运维'],
      // 下拉框选项
      opt: {
        project: [],
        mchList: []
      },
      // 工具栏查询条件
      query: {
        project: '',
        mch: '',
        date: dayjs().format('YYYY-MM-DD')
      },
      // 萤石云视频地址
      VideoH5Url: null,
      loading: false,
      // 报警信息
      alert: {
        operating: [],
        maintenance: [],
        weather: []
      },
      // 维保周期
      maintenanceDuration: [
        { name: '1-1-17', startTime: '2022-3-18 09:00:00', endTime: '2022-3-18 17:00:00' }
      ],
      // 曲线数据筛选
      checkAll: false,
      checkedCities: [],
      defaultChecked: [
        'r---1',
        'r---2',
        'r---3',
        'r---4',
        'r_032',
        'r_036',
        'r_025',
        'r_010',
        'r_047',
        'fpie_002_11',
        'fpie_002_09'
      ],
      rules1: ['r---1', 'r---2'],
      rules2: [
        'r---3',
        'r---4',
        'r_032',
        'r_036',
        'r_025',
        'r_010',
        'r_047',
        'fpie_002_11',
        'fpie_002_09'
      ],
      curveOpt: [],
      isIndeterminate: true,
      visible: false,
      checkAll2: false,
      checkedCities2: [],
      defaultChecked2: [1, 3],
      curveOpt2: [],
      isIndeterminate2: true,
      visible2: false,
      // 曲线类型
      curveType: 0,
      // 切换曲线参数弹出窗的宽度
      curveOptChangeDialogWidth: 550,
      // 曲线数据
      seriesData: [],
      xAxisData: [],
      // 曲线的类型，时间轴、类目轴
      type: 'time',
      // type: 'category',
      // 初次加载标志
      onceFlag: true,
      showFlag: true
    }
  },
  watch: {
    curveType(nv) {
      this.visible = false
      this.visible2 = false
    },
    'query.mch': {
      handler: function (nv, ov) {
        // 项目机组列表仅包含机组编号和名字，需查询完整机组信息
        this.$api.screenData
          .getsingleMchInfo({ id: nv })
          .then((value) => {
            const data = value.Data
            data && this.toggleSelectCrew(data)
          })
          .catch((result) => {
            console.error(result)
          })
        if (!!nv) {
          this.getChartData()
          this.getBaseInfo()
        } else {
          this.$message('请选择机组！')
        }
      }
    },
    'query.date': {
      handler: function (nv, ov) {
        if (!!this.query.mch) {
          this.getChartData()
          this.getBaseInfo()
        } else {
          this.$message('请选择机组！')
        }
      }
    },
    // 根据所选项目筛选机组（不选表示全部机组）
    'query.project': {
      handler: function (nv, ov) {
        // 切换项目
        this.$api.project.GetGensetProject({ id: nv }).then((value) => {
          const data = value.Data
          if (data) this.toggleCurrentProject(data)
        })
        this.getMchList()
      }
    },
    curveType(nv) {
      this.visible = false
      this.visible2 = false
      this.getChartData()
    }
  },
  computed: {
    // vuex
    ...mapGetters(['selectCrew', 'currentProject'])
  },
  methods: {
    // vuex
    ...mapActions(['toggleSelectCrew', 'toggleCurrentProject']),
    // 获取机组项目基础信息
    getBaseInfo() {
      const generatorSetId = this.query.mch
      const query = {
        GeneratorSetId: generatorSetId
        // GeneratorSetId: 6455
      }
      this.$api.operation.getGensetProjectGensetDetail({}, query).then((res) => {
        const data = !!res.Data && res.Data
        if (data) {
          this.baseInfo = { ...data }
          try {
            // 格式化时间
            this.baseInfo.GensetProject.StartDate = formatTime(
              this.baseInfo.GensetProject.StartDate
            ).format('YYYY-MM-DD')
            this.baseInfo.GensetProject.EndDate = formatTime(
              this.baseInfo.GensetProject.EndDate
            ).format('YYYY-MM-DD')
            // TODO 项目类型？
            this.baseInfo.GensetProject.ProjectType =
              this.projectTypeMapping[this.baseInfo.GensetProject.ProjectType]
          } catch (error) {}
        }
      })
      // 获取报警记录
      const paramsAlert = {
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs(this.query.date).add(1, 'd').format('YYYY-MM-DD 00:00:00'),
        Mode: '-1'
      }
      const queryAlert = {
        id: this.query.mch
      }
      this.$api.operation
        .findGeneratorSetHistoryAlarm(paramsAlert, queryAlert)
        .then((res) => {
          const alarms = !!res.Data && !!res.Data.Alarms && res.Data.Alarms
          if (alarms) {
            const operating = new Array()
            alarms.forEach((item) => {
              const obj = {
                time: formatTime(item.StartTime).format('yyyy-MM-DD HH:mm:ss'),
                title: item.TypeName,
                content: item.Describe
              }
              operating.push(obj)
            })
            this.alert.operating = operating
          }
        })
        .catch((err) => {
          this.$message({
            message: '获取报警记录了信息失败，请检查网络！',
            type: 'warning'
          })
          console.error(err)
        })
      // 获取维保记录
      const paramsMaintenance = {
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs(this.query.date).add(1, 'd').format('YYYY-MM-DD 00:00:00')
      }
      const queryMaintenance = {
        GeneratorSetId: this.query.mch
      }
      this.$api.operation
        .getGensetProjectGensetMaintenanceRemind(paramsMaintenance, queryMaintenance)
        .then((res) => {
          const alarms = !!res.Data && res.Data
          if (alarms) {
            const maintenance = new Array()
            alarms.forEach((item) => {
              const obj = {
                time: formatTime(item.NextRemindDate).format('yyyy-MM-DD HH:mm:ss'),
                title: '维保提醒',
                content: item.ItemContent
              }
              maintenance.push(obj)
            })
            this.alert.maintenance = maintenance
          }
        })
        .catch((err) => {
          this.$message({
            message: '获取报警记录了信息失败，请检查网络！',
            type: 'warning'
          })
          console.error(err)
        })
      // TODO 暂无 获取天气记录
    },
    // 获取项目列表，首次加载获取，默认选中第一个项目
    getProjectList() {
      this.$api.operation
        .findGensetProjectIndexProject()
        .then((res) => {
          const data = !!res.Data && res.Data
          if (data && data.length && data.length > 0) {
            const projectList = new Array()
            data.forEach((item) => {
              const obj = {
                value: item.ProjectId,
                label: item.ProjectName
              }
              projectList.push(obj)
            })
            this.opt.project = projectList
            this.$nextTick(function () {
              this.getMchListFirst()
            }) // this.$nextTick(function () {
            //   this.query.project = projectList[0].value
            // })
          }
        })
        .catch((err) => {
          this.$message({
            message: '获取项目列表信息失败，请检查网络！',
            type: 'warning'
          })
          console.error(err)
        })
    },
    // 获取机组列表（根据所选项目）
    getMchList() {
      const params = { ProjectId: this.query.project }
      this.$api.operation
        .findGensetProjectIndexGenset(params)
        .then((res) => {
          const data = !!res.Data && res.Data
          if (data && data.length && data.length > 0) {
            const mchList = new Array()
            data.forEach((item) => {
              const obj = {
                value: item.GeneratorSetId,
                label: item.DisplayName
              }
              mchList.push(obj)
            })
            this.opt.mchList = mchList
            this.$nextTick(function () {
              if (!this.onceFlag) this.query.mch = mchList[0].value
              else {
                // 初次加载标志反转
                this.onceFlag = false
                const query = this.$route.query
                let crewId = undefined
                if (query) crewId = query.crewId
                if (!!crewId) {
                  this.query.mch = Number(crewId)
                } else {
                  this.query.mch = this.selectCrew.ID
                }
              }
            })
          } else {
            this.$message('该项目未绑定机组！')
            this.opt.mchList = []
            this.query.mch = ''
          }
        })
        .catch((err) => {
          this.$message({
            message: '获取机组列表信息失败，请检查网络！',
            type: 'warning'
          })
          console.error(err)
        })
    },
    // 获取机组列表（初次获取信息）
    getMchListFirst() {
      let crewId = this.selectCrew && !!this.selectCrew.ID && this.selectCrew.ID
      // 打开新页面,则使用传过来的参数
      const query = this.$route.query
      if (query) {
        const type = query.type
        const crewID = query.crewId
        if (type === 'newPage') {
          crewId = crewID
        }
      }

      // 根据机组获取项目
      !!crewId &&
        this.$api.operation
          .getGensetProjectGensetDetail({}, { GeneratorSetId: crewId })
          .then((value) => {
            const data = value.Data
            if (data) {
              const projectId = data.GensetProject && data.GensetProject.ProjectId
              if (projectId) {
                this.opt.project.forEach((e) => {
                  if (e.value.toString() === projectId.toString()) {
                    this.query.project = e.value
                  }
                })
              } else {
                this.query.project = this.opt.project[0].value
              }
            }
          })
    },
    // 获取曲线参数
    getCurveOpt() {
      // TODO 测试数据，曲线参数，需更改为从服务器获取
      const text = [
        '转速',
        '电瓶电压',
        '废液液位',
        '有功强度',
        '风扇转速',
        '无功强度',
        '三相电压',
        '三相电流'
      ]
      const text2 = ['日功率', '日运行时间', '有功冲击负荷捕捉', '无功冲击捕捉', '某参数']
      const arr = []
      const arr2 = []
      text.forEach((item, index) => {
        const obj = {
          value: index + 1,
          label: item
        }
        arr.push(obj)
      })
      text2.forEach((item, index) => {
        const obj2 = {
          value: index + 1,
          label: item
        }
        arr2.push(obj2)
      })
      this.curveOpt = [
        {
          value: 'r---1',
          label: '有功冲击负荷强度'
        },
        {
          value: 'r---2',
          label: '无功冲击负荷强度'
        },
        {
          value: 'r---3',
          label: '平均负荷'
        },
        {
          value: 'r---4',
          label: '负荷强度'
        },
        {
          value: 'r_032',
          label: '有功功率'
        },
        {
          value: 'r_036',
          label: '无功功率'
        },
        {
          value: 'r_025',
          label: '电流'
        },
        {
          value: 'r_010',
          label: '电压'
        },
        {
          value: 'r_047',
          label: '转速'
        },
        {
          value: 'fpie_002_11',
          label: 'APF功率因数'
        },
        {
          value: 'fpie_002_09',
          label: '谐波含量'
        }
      ]
      this.curveOpt2 = arr2
    },
    // 获取曲线数据
    getChartData() {
      let items = []
      this.checkedCities.forEach((item) => {
        items = [...items, ...item.split(',')]
      })
      const params = {
        TimeStart: dayjs(this.query.date).add(-7, 'd').format('YYYY-MM-DD 23:59:59'),
        TimeEnd: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        Items: items.join(',')
      }
      const query = {
        GeneratorSetId: this.query.mch
      }
      this.loading = true
      this.$api.operation.findGeneratorSetHistoryDataV4(params, query).then((value) => {
        const list = value.Data && value.Data.Datas
        if (Array.isArray(list) && list.length > 0) {
          // 有功无功最高值描一个点，其他每个小时取一个点
          const rules1 = this.rules1
          const rules2 = this.rules2
          const seriesData = new Array()
          list.forEach((e) => {
            const obj = {
              name: e.Describe,
              // yAxisIndex: 0,
              data: []
            }
            if (rules1.includes(e.No)) {
            }
            if (rules2.includes(e.No)) {
              const tempContainer = []
              e.Data &&
                e.Data.forEach((item) => {
                  const h = item.TimeString.split(' ')[1].split(':')[0]
                  const d = item.TimeString.split(' ')[0].split('-')[2]
                  const f = d + '-' + h
                  if (!tempContainer.includes(f)) {
                    tempContainer.push(f)
                    const arr = [+formatTimeDayjs(item.Time), item.Value, item.TimeString]
                    obj.data.push(arr)
                  }
                })
            }
            seriesData.push(obj)
          })
          this.seriesData = seriesData
          this.loading = false
        }
        this.loading = false
      })
      // old code
      // const checkedCities = this.curveType === 0 ? 'checkedCities' : 'checkedCities2'
      // const curveOpt = this.curveType === 0 ? 'curveOpt' : 'curveOpt2'
      // const optMapping = new Map()
      // const seriesNames = []
      // const time = {
      //   // 七天前
      //   begin: dayjs(this.query.date).add(-6, 'd').hour(0).minute(0).second(0),
      //   end: dayjs(this.query.date),
      //   interval: 1
      // }
      // function getRandom(begin, end) {
      //   return Number((Math.random() * (end - begin) + begin).toFixed(0))
      // }
      // this[curveOpt].forEach((e) => {
      //   optMapping.set(e.value, e.label)
      // })
      // this[checkedCities].forEach((e) => {
      //   seriesNames.push(optMapping.get(e))
      // })
      // const seriesData = []
      // const xAxisData = []
      // seriesNames.forEach((e, index) => {
      //   const obj = {
      //     name: e,
      //     data: []
      //   }
      //   const begin = getRandom(100, 1000)
      //   const end = getRandom(begin, begin + 50)
      //   for (
      //     // 七天前
      //     let i = dayjs(this.query.date).add(-6, 'd').hour(0).minute(0).second(0);
      //     i <= time.end;
      //     i = i.add(time.interval, 'd')
      //   ) {
      //     if (this.type === 'time') {
      //       obj.data.push([+i, getRandom(begin, end)])
      //     } else {
      //       obj.data.push(getRandom(begin, end))
      //       index !== 0 || xAxisData.push(i.format('YYYY-MM-DD'))
      //     }
      //   }
      //   seriesData.push(obj)
      // })
      // this.seriesData = seriesData
      // this.xAxisData = xAxisData
    },
    // 跳转到项目策略配置页
    handleLinkToSC() {
      this.$router.push({
        path: '/equipment/crewinfo-config',
        query: {
          activeTab: 14
        }
      })
    },
    // *** 曲线筛选条件相关
    handleCheckAllChange(val) {
      const curveOptValues = []
      this.curveOpt.forEach((item) => {
        curveOptValues.push(item.value)
      })
      this.checkedCities = val ? curveOptValues : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.curveOpt.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.curveOpt.length
    },
    handleCheckAllChange2(val) {
      const curveOptValues = []
      this.curveOpt2.forEach((item) => {
        curveOptValues.push(item.value)
      })
      this.checkedCities2 = val ? curveOptValues : []
      this.isIndeterminate2 = false
    },
    handleCheckedCitiesChange2(value) {
      let checkedCount = value.length
      this.checkAll2 = checkedCount === this.curveOpt2.length
      this.isIndeterminate2 = checkedCount > 0 && checkedCount < this.curveOpt2.length
    },
    // 更新曲线图
    handleCurveParamChange() {
      this.visible = false
      this.visible2 = false
      this.getChartData()
    },
    handleShowBtn() {
      const f = this.showFlag
      const msg = f ? '收起' : '展开'
      this.$message.success(`项目信息已${msg}!`)
      this.showFlag = !f
    }
  },
  mounted() {
    this.checkedCities = this.defaultChecked
    this.checkedCities2 = this.defaultChecked2
    this.getCurveOpt()
    this.getProjectList()
    // setTimeout(() => {
    //   this.query.mch = this.selectCrew.ID || ''
    // }, 300)
  }
}
</script>

<template>
  <div>
    <BMainTopComponent />
    <div class="operational-chart-weekly">
      <div class="info-container">
        <div>
          <div class="info-container-row">
            <div class="press" @click="handleShowBtn">
              {{ showFlag ? '收起' : '展开' }}
            </div>
            <my-cell title="项目信息" :isHead="1" :col="1"></my-cell>
            <my-cell title="机组信息" :isHead="1"></my-cell>
            <my-cell title="配置信息" :isHead="1"></my-cell>
          </div>
          <div class="info-container-row" v-show="showFlag">
            <my-cell title="项目编号：" :value="baseInfo.GensetProject.ProjectId"> </my-cell>
            <my-cell title="项目名称：" :value="baseInfo.GensetProject.ProjectName"> </my-cell>
            <my-cell title="发动机型号：" :value="baseInfo.EngineModel"> </my-cell>
            <my-cell title="发电机型号：" :value="baseInfo.GeneratorModel"> </my-cell>
            <my-cell title="限制功率：" :value="'/'"></my-cell>
            <my-cell title="额定功率：" :value="baseInfo.RatedPower"></my-cell>
          </div>
          <div class="info-container-row" v-show="showFlag">
            <my-cell title="项目类型：" :value="baseInfo.GensetProject.ProjectType"></my-cell>
            <my-cell title="项目时间：" :value="baseInfo.GensetProject.StartDate"></my-cell>
            <my-cell title="控制器型号：" :value="baseInfo.CollectorModel" :col="2"></my-cell>
            <my-cell title="额定转速：" :value="baseInfo.RatedSpeed"></my-cell>
            <my-cell title="额定电流：" :value="baseInfo.RatedCurrent"></my-cell>
          </div>
          <div class="info-container-row" v-show="showFlag">
            <my-cell
              title="项目地址："
              :value="baseInfo.GensetProject.ProjectAddress"
              :col="2"
            ></my-cell>
            <my-cell title="" value="" :col="2"></my-cell>
            <my-cell title="额定频率：" :value="baseInfo.RatedFrequency"></my-cell>
            <my-cell title="额定电压：" :value="baseInfo.RatedVoltage"></my-cell>
          </div>
          <div class="info-container-row" v-show="showFlag">
            <my-cell title="环境温度：" :value="baseInfo.Temperature"></my-cell>
            <my-cell title="海拔：" :value="baseInfo.Altitude"></my-cell>
            <my-cell title="累计运行时长：" :value="baseInfo.RunHourTotal"></my-cell>
            <my-cell title="当前维保节拍：" :value="'●暂无'"></my-cell>
          </div>
          <div class="info-container-row" v-show="showFlag">
            <my-cell title="启动次数：" :value="baseInfo.BootTimes"></my-cell>
            <my-cell title="发电量：" :value="baseInfo.TotalElectricalEnergy"></my-cell>
            <my-cell title="平均负荷强度：" :value="'●暂无'"></my-cell>
            <div class="strategy-btn">
              <el-button
                size="mini"
                type="primary"
                title="点击跳转项目维保策略配置页"
                @click="handleLinkToSC"
                >策略优化</el-button
              >
            </div>
          </div>
          <!-- 20220829 周一 日运维图和周运维暂不显示字段：是否可用、启动状态、投入状态 -->
          <!-- <div class="info-container-row" v-show="showFlag">
            <my-cell title="是否可用：" :value="'●暂无'"></my-cell>
            <my-cell title="启动状态：" :value="'●暂无'"></my-cell>
            <my-cell title="投入状态：" :value="'●暂无'"></my-cell>
          </div> -->
        </div>
      </div>
      <div class="alert-container" v-show="showFlag">
        <el-row>
          <el-col :span="5">
            <el-row class="title" type="flex" align="middle" justify="center">
              设备报警记录
            </el-row>
          </el-col>
          <el-col :span="19">
            <el-row class="a-row" v-for="(e, i) in alert.operating" :key="i">
              <el-col :span="6">1.事件时间：{{ e.time }}</el-col>
              <el-col :span="18">报警名称：{{ e.content }}</el-col>
              <!-- 以下数据无数据支撑 -->
              <!-- <el-col :span="4">已处理</el-col>
              <el-col :span="4">远程调参</el-col>
              <el-col :span="4">AI工程师</el-col> -->
            </el-row>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-row class="title" type="flex" align="middle" justify="center">
              维保提示记录
            </el-row>
          </el-col>
          <el-col :span="19">
            <el-row class="a-row" v-for="(e, i) in alert.maintenance" :key="i">
              <el-col :span="6">1.事件时间：{{ e.time }}</el-col>
              <el-col :span="18">报警名称：{{ e.content }}</el-col>
              <!-- 以下数据无数据支撑 -->
              <!-- <el-col :span="4">已处理</el-col>
              <el-col :span="4">远程调参</el-col>
              <el-col :span="4">AI工程师</el-col> -->
            </el-row>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="5">
            <el-row class="title" type="flex" align="middle" justify="center">
              环境风险提示
            </el-row>
          </el-col>
          <el-col :span="19">
            <el-row class="a-row" v-for="(e, i) in alert.Temperature" :key="i">
              <el-col :span="6">1.事件时间：{{ e.time }}</el-col>
              <el-col :span="18">报警名称：{{ e.content }}</el-col>
              <!-- 以下数据无数据支撑 -->
              <!-- <el-col :span="4">已处理</el-col>
              <el-col :span="4">远程调参</el-col>
              <el-col :span="4">AI工程师</el-col> -->
            </el-row>
          </el-col>
        </el-row>
      </div>
      <el-row class="toolbox-container" :gutter="10">
        <el-col :span="5"
          >项目：
          <el-select placeholder="请选择" v-model="query.project">
            <el-option
              v-for="item in opt.project"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="5"
          >机组：
          <el-select v-model="query.mch" no-data-text="该项目未绑定机组">
            <el-option
              v-for="item in opt.mchList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="5"
          >结束日期：<el-date-picker
            :clearable="false"
            v-model="query.date"
            type="date"
            placeholder="选择日期"
            placement="bottom-start"
          >
          </el-date-picker
        ></el-col>
        <!-- [x] 修复点击切换图表类型时，弹出框会闪烁一下 将v-if改为v-show -->
        <el-col :span="5" class="curve-type" style="display: none"
          >曲线类型：
          <el-button plain size="mini" @click="curveType = 0" :checked="!curveType"
            >负载曲线</el-button
          >
          <el-button size="mini" plain @click="curveType = 1" :checked="!!curveType"
            >维保曲线</el-button
          >
        </el-col>
        <el-col :span="4"
          >曲线数据：
          <!-- 负载曲线数据 -->
          <el-popover
            v-show="!curveType"
            v-model="visible"
            placement="top"
            :width="curveOptChangeDialogWidth"
            trigger="manual"
          >
            <div>
              <el-checkbox
                :indeterminate="isIndeterminate"
                v-model="checkAll"
                @change="handleCheckAllChange"
                >全选</el-checkbox
              >
              <el-button
                style="position: absolute; right: 45px; top: 1px"
                @click="visible = false"
                type="text"
                >关闭</el-button
              >
              <el-button
                @click="handleCurveParamChange"
                style="position: absolute; right: 5px; top: 1px"
                type="text"
                >确定</el-button
              >
              <div style="margin: 15px 0"></div>
              <el-row>
                <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                  <el-col v-for="(item, index) in curveOpt" :key="index" :span="6"
                    ><el-checkbox :label="item.value">
                      <span
                        :style="{
                          whiteSpace: 'normal'
                        }"
                      >
                        {{ item.label }}</span
                      >
                    </el-checkbox></el-col
                  >
                </el-checkbox-group></el-row
              >
            </div>
            <el-button slot="reference" @click="visible = true" plain size="mini"
              >曲线参数选择</el-button
            >
          </el-popover>
          <!-- 维保曲线数据 -->
          <el-popover
            v-show="!!curveType"
            v-model="visible2"
            placement="bottom"
            :width="curveOptChangeDialogWidth"
            trigger="manual"
          >
            <div>
              <el-checkbox
                :indeterminate="isIndeterminate2"
                v-model="checkAll2"
                @change="handleCheckAllChange2"
                >全选</el-checkbox
              >
              <el-button
                style="position: absolute; right: 45px; top: 1px"
                @click="visible2 = false"
                type="text"
                >关闭</el-button
              >
              <el-button
                @click="handleCurveParamChange"
                style="position: absolute; right: 5px; top: 1px"
                type="text"
                >确定</el-button
              >
              <div style="margin: 15px 0"></div>
              <el-row>
                <el-checkbox-group v-model="checkedCities2" @change="handleCheckedCitiesChange2">
                  <el-col v-for="(item, index) in curveOpt2" :key="index" :span="6"
                    ><el-checkbox :label="item.value">
                      <span
                        :style="{
                          whiteSpace: 'normal'
                        }"
                      >
                        {{ item.label }}</span
                      >
                    </el-checkbox></el-col
                  >
                </el-checkbox-group></el-row
              >
            </div>
            <el-button slot="reference" @click="visible2 = true" plain size="mini"
              >点击切换</el-button
            >
          </el-popover>
        </el-col>
      </el-row>
      <div v-loading="loading" element-loading-text="数据加载中..." class="chart-container">
        <my-chart
          v-if="!curveType"
          :seriesData="seriesData"
          :xAxisData="xAxisData"
          :type="type"
          :showFlag="showFlag"
          :curveType="curveType"
        ></my-chart>
        <my-chart
          v-if="!!curveType"
          :seriesData="seriesData"
          :xAxisData="xAxisData"
          :type="type"
          :showFlag="showFlag"
          :curveType="curveType"
        ></my-chart>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
@borderColor: #7fb1df;

.operational-chart-weekly {
  // [x] 修复qq浏览器flex布局问题
  // display: block !important;
  height: 100%;
  /deep/ .el-loading-mask {
    background-color: rgba(17, 36, 70, 0.6);
  }
  .info-container {
    display: flex;
    & > div:first-child {
      flex: 3;
      border-right: 0;
      border: 1px solid @borderColor;
      border-right: 0;
    }
    .video-container {
      flex: 1;
      height: 210px;
      border-left: 0;
      border: 1px solid @borderColor;
    }
    &-row {
      display: flex;
      position: relative;
      .press {
        left: 8px;
        cursor: pointer;
        font-weight: 600;
        color: #209a86;
        line-height: 35px;
        font-size: 1.1rem;
        position: absolute;
      }
    }
    .strategy-btn {
      right: 0;
      height: 35px;
      display: flex;
      position: absolute;
      align-items: center;
      border-right: 1px solid rgb(127, 177, 223);
      border-bottom: 1px solid rgb(127, 177, 223);
      > .el-button {
        height: 100%;
        border-radius: 0;
      }
    }
  }
  .alert-container {
    border-left: 1px solid @borderColor;
    > .el-row > .el-col {
      height: 61px;
      border-right: 1px solid @borderColor;
      border-bottom: 1px solid @borderColor;
      &:last-child {
        overflow: auto;
      }
    }
    .title {
      height: 60px;
    }
    .a-row {
      padding: 0 0 0 5px;
      > .el-col {
        height: 30px;
        display: flex;
        align-items: center;
      }
    }
  }
  .toolbox-container {
    display: flex;
    padding: 5px 0;
    margin-top: 20px;
    .el-col {
      display: flex;
      align-items: center;
      white-space: nowrap;
    }
    .curve-type {
      /deep/.el-button + .el-button {
        margin-left: 0;
      }
      /deep/.el-button {
        &:first-child {
          border-start-end-radius: 0;
          border-end-end-radius: 0;
          border-right: 0;
        }
        &:last-child {
          border-start-start-radius: 0;
          border-end-start-radius: 0;
          border-left: 0;
        }
        &[checked] {
          background: #1abc9c !important;
          color: white !important;
          border-right: 1px solid #dcdfe6;
          border-left: 1px solid #dcdfe6;
        }
      }
    }
  }
  .chart-container {
    height: 400px;
  }
}
</style>



